import moment from "moment";
import React from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { MediaBaseUrl } from "../../../../actions/api";
import TopcardSkeleton from "../../analytics-new/layout/skeleton/topcardSkeleton";

    const BookCall = (props) => {

    const hour = moment().utc().hour();

    const { allWalllist, allfeedsCount, alltotalPostcount, numberOfActiveWalls, userData, loader } = props

    const totalData = [{ 'name' : 'Total Walls', 'icon' : 'home-walls', 'count' : allWalllist && Object.keys(allWalllist).length ? allWalllist.length : 0, 'tooltip' : 'Total Walls','border' : '0' },
                       { 'name' : 'Total Feeds', 'icon' : 'home-feeds',  'count' : allfeedsCount, 'tooltip' : 'Total Feeds','border' : '2' },
                       { 'name' : 'Total Post', 'icon' : 'home-post', 'count' : alltotalPostcount, 'tooltip' : 'Total Post','border' : '1' },
                       { 'name' : 'Active Screen', 'icon' : 'home-posted', 'count' : numberOfActiveWalls, 'tooltip' : 'Active Screen','border' : '3' }
                  ]

    return (
        <>
            <div className="customer-support-main border rounded-8x p-10 mb-5">
                <div className="row align-items-center">
                    <div className="col-md-6">
                    <div className="customer-info mb-5">
                        <h4>Hey, {userData.user.name}</h4>
                        <p> { hour >= 6 && hour < 12 ? 'Good Morning' : 'Good Evening' } {moment().utc().format('DD MMMM YYYY')}</p>
                        <div className="mt-6">
                        <a href="" className="btn btn-outline-dark me-3 btn-min-w">
                            <span className="position-relative">Chat Now</span>
                        </a>
                        <a href="" className="btn btn-primary btn-min-w">
                            Book A Call
                        </a>
                        </div>
                    </div>
                    </div>
                    <div className="col-md-6">
                    <div className="customer-success-manager rounded-8x">
                        <div className="d-flex success-manager-info py-5 px-9 rounded-8x">
                        <div className="customer-success-details col-8">
                            <p className="text-secondary text-uppercase font-size-lg">
                            vivek from socail walls
                            </p>
                            <p className="text-dark font-size-lg">
                            Unveil the Power of Tagshop: How Shop LC Skyrocked $20k in
                            Revenue Last Month! Discover Their Secret Formula!
                            </p>
                            <a
                            href=""
                            className="text-decoration-underline text-dark font-size-h5"
                            >
                            Get Your Socail Wall
                            </a>
                        </div>
                        <div className="customer-success-image position-relative col-4">
                            <img
                            className="tb_cp_image"
                            role="img"
                            src="https://s3.us-west-1.wasabisys.com/cloud.tagbox.com/react-app/media/images/customer-success.png"
                            height="233"
                            width="239"
                            />
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
            <div className="row">

            { loader ? <TopcardSkeleton res={3} col={4} /> : totalData.map((e)=>{
                return(<div className="col-sm-6 col-lg-3 mb-5">
                    <div className={`card-border-top top-border-${e.border} p-5 card-shadow card`}>
                        <div className="d-flex align-items-center">
                            <div className="icon-box d-flex align-items-center justify-content-center rounded-8x me-3">
                                <img src={`${MediaBaseUrl}icons/svg-icons/${e.icon}.svg`}/>
                            </div>
                            <div className="">
                                <span className="font-weight-bold font-size-h6 mb-2 text-secondary"> {e.name}
                                    <OverlayTrigger
                                        placement="top"
                                        overlay={  <Tooltip>{e.tooltip}</Tooltip>}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" className="ms-1"><g id="Group_5749" data-name="Group 5749" transform="translate(12215 -4805)"><rect id="Rectangle_2065" data-name="Rectangle 2065" width="24" height="24" transform="translate(-12215 4805)" fill="none" opacity="0"></rect><path id="Path_6691" data-name="Path 6691" d="M20,11a9,9,0,1,0,9,9A9,9,0,0,0,20,11Zm.923,12.692a.923.923,0,0,1-1.846,0V19.308a.923.923,0,1,1,1.846,0ZM20,17.231a.923.923,0,1,1,.923-.923A.923.923,0,0,1,20,17.231Z" transform="translate(-12223 4797)" fill="#959595" opacity="0.999"></path></g></svg>
                                   </OverlayTrigger>     
                                </span>
                                <h4 className="font-size-h1 mb-0"> {e.count}</h4>
                            </div>
                        </div>
                    </div>
                </div>)
               })}

             </div>
        </>
    );
 };

 export default React.memo(BookCall)